@use "base/variables" as v;
@use "base/mixins" as m;

.contenedor-boletos {
    background-color: v.$verde;
    
    h3{
        color: v.$blanco;
    }
    
    .boletos {
        @include m.contenedor;
        @include m.tablet {
            @include m.grid(2,5);
        }
    }
}



.pase {

    transition-property: transform;
    transition-duration: .3s;
    transition-timing-function: ease-in-out;

    &:hover {
        transform: scale(1.1);
    }
    
    &-nombre {
        font-size: 3.5rem;
        font-weight: 900;
        text-align: center;
        color: v.$blanco;
    }
    
    &-header,
    &-footer {
        padding: .5rem;
        
        p {
            text-align: center;
            color: v.$blanco;
            margin: 0;
        }

    }

    &-header {
        background-color: v.$rosa;
        p {
            text-transform: uppercase;
            font-size: 2rem;
            padding: 1rem 0;
        }
    }
    
    &-body {
        background-color: v.$blanco;

        ul {
            @include m.resetear-lista;

            li {
                padding: 3rem;
                font-size: 2rem;
                border-bottom: .1rem solid #e1e1e1;
            }
        }
    }
    
    &-footer {
        background-color: v.$blanco;
        padding: 3rem;

        p {
            font-size: 3rem;
            font-weight: 900;
            background-color: v.$morado;
            padding: 1rem;
            border-radius: 1rem;
            cursor: pointer;

            transition-property: background-color;
            transition-duration: .3s;
            transition-timing-function: ease-in-out;

            &:hover {
                background-color: darken(v.$morado, 20%);
                
            }
        }
    }
    
}